import React from 'react'
import Layout from '../component/Layout';

const aboutData = [
    {
        id:1,
        name:"Faculty of Engineering,Chiang Mai University"
        
    }
]
const aboutSkill = [
    {
        id:1,
        name:"CSS"
    },
    {
        id:2,
        name:"HTML"
    },
    {
        id:3,
        name:"ReactJS"
    },
    {
        id:4,
        name:"Java"
    }
]
const aboutme = () => {
    return (
        <Layout>
        <div>
        <h1>Education</h1>
        <p>Computer Engineering</p>
        <p>2018-now</p>
            {aboutData.map(x=>{
                return (
                    <li key={x.id}>
                        {x.name}
                    </li>
                )
            })}
        <p>Skills</p>
            {aboutSkill.map(x=>{
                return(
                    <li key={x.id}>
                        {x.name}
                    </li>
                )
            })}
        </div>
        </Layout>
    )
}

export default aboutme;
